import React from 'react'
import styled from 'styled-components'

const Container = styled.div`
	display: flex;
	justify-content: center;
	align-items: center;
	margin-top: 20px;
	width: 100%;
	height: 50%;
`
const InnerDiv = styled.div`
	z-index: 2;
	background: #fff;
	width: 50%;
	padding: 50px;
	margin-top: 175px;
	p {
		font-size: 1.2em;
	}
	@media (min-width: 1000px) {
		padding-left: 75px;
		padding-bottom: 200px;
		margin-top: 350px;
	}
`

export default () => (
	<Container>
		<InnerDiv>
			<p><strong>Seaboard Seafood Inc.</strong></p>
			<p>Sales Office: Ajax, ON. Canada</p>
			<p><strong>T</strong> 1.416.846.4990</p>
			<p><strong>F</strong> 1.905.427.8433</p>
			<p><strong>E</strong> johnkimseafood@gmail.com</p>
			<p><strong>www.seaboardseafood.com</strong></p>
		</InnerDiv>
	</Container>
)


